.wave {
  animation-name: wave-animation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
  font-size: 2.5rem;

  @media screen and (min-width: 2000px) {
    font-size: 5rem;
  }
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

// Smaller heading text for the "i am into" section
.head-text-smaller {
  font-size: 2.375rem; // 5px smaller than head-text
  font-weight: 800;
  text-align: center;
  color: var(--black-color);
  text-transform: capitalize;
  margin: 0.25rem 0;

  span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 3.625rem; // 5px smaller than head-text at this breakpoint
  }

  @media screen and (max-width: 450px) {
    font-size: 1.625rem; // 5px smaller than head-text at this breakpoint
  }
}

// Even smaller heading text for the role section
.head-text-mini {
  font-size: 1.875rem; // Even smaller than head-text-smaller
  font-weight: 700;
  text-align: left;
  color: var(--black-color);
  text-transform: capitalize;
  margin: 0.15rem 0;

  span {
    color: var(--secondary-color);
  }

  @media screen and (min-width: 2000px) {
    font-size: 3rem;
  }

  @media screen and (max-width: 450px) {
    font-size: 1.25rem;
  }
}

// Role section container (separate from main text)
.role-section-container {
  margin: 2rem 0;
  align-self: flex-start;
  text-align: left;
  margin-left: 100px;
  width: 100%; // Align with the wave/main text

  @media screen and (max-width: 1200px) {
    margin: 1.5rem 0;
    margin-left: 100px;
  }

  @media screen and (max-width: 450px) {
    margin: 1rem 0;
    margin-left: 100px;
  }
}

// Main text section (with wave)
.main-text-section {
  margin-left: 0; // Remove the margin since we'll align the container instead
}

// Social section aligned like button
.social-section {
  margin-top: 1.5rem;
  margin-left: 100px; // Align with the wave/main text
  align-self: flex-start; // Left aligned

  .app__social {
    display: flex;
    flex-direction: row;
    justify-content: flex-start; // Left aligned
    align-items: center;
    padding: 0;
    gap: 0.75rem;

    a {
      width: 40px;
      height: 40px;
      border-radius: 50%;

      // Change these to match the about button's default appearance:
      background-color: var(--secondary-color);
      border: 2px solid var(--secondary-color);

      display: flex;
      justify-content: center;
      align-items: center;
      transition: all 0.3s ease-in-out;
      text-decoration: none;

      svg {
        width: 15px;
        height: 15px;
        color: var(--white-color); // Change from #4a4a4a to match button
      }

      &:hover {
        // Apply the original button's default styling as hover:
        background-color: var(--white-color);
        border-color: var(--secondary-color);

        svg {
          color: var(--secondary-color);
        }
      }

      @media screen and (min-width: 2000px) {
        width: 70px;
        height: 70px;

        svg {
          width: 30px;
          height: 30px;
        }
      }
    }
  }

  @media screen and (max-width: 1200px) {
    margin-top: 1rem;
    margin-left: 100px;
  }
}

// Typing animation for the role text
.typing-text {
  color: rgb(148, 8, 8);
  position: relative;

  &.typing {
    animation: typeIn 0.5s ease-in-out;
  }

  &.deleting {
    animation: typeOut 0.5s ease-in-out;
  }
}

@keyframes typeIn {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes typeOut {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-20px);
  }
}

// Button styling
.home-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 1rem 2rem;

  // Change these to make it look like the hover state by default:
  background-color: transparent;
  color: var(--secondary-color);
  border: 2px solid var(--secondary-color);

  text-decoration: none;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: 500;
  margin-top: 1rem;
  margin-left: 100px;
  transition: all 0.3s ease-in-out;
  align-self: flex-start;

  span {
    color: var(--secondary-color);
  }

  i {
    font-size: 1.2rem;
    color: var(--secondary-color);
  }

  /* &:hover {
    background-color: transparent;
    color: var(--secondary-color);
    transform: translateY(-2px);
    box-shadow: 0 5px 15px rgba(49, 59, 172, 0.3);

    span,
    i {
      color: var(--secondary-color);
    }
  } */

  @media screen and (min-width: 2000px) {
    padding: 1.25rem 2.5rem; // Made bigger
    font-size: 1.4rem; // Made bigger
    margin-top: 1.5rem;
    margin-left: 100px;

    i {
      font-size: 1.6rem; // Made bigger
    }
  }

  @media screen and (max-width: 450px) {
    padding: 0.8rem 1.5rem; // Made bigger
    font-size: 1rem; // Made bigger
    margin-top: 0.75rem;
    margin-left: 100px;

    i {
      font-size: 1.1rem; // Made bigger
    }
  }
}

#home {
  position: relative;
  background-color: var(--primary-color);
  min-height: 100vh;

  @media screen and (max-width: 1200) {
    height: 150vh;
  }
  @media screen and (max-width: 450) {
    height: 150vh;
  }
  // Particles.js background
  #particles-js {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  flex-direction: row;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;

  padding: 2rem 3rem;

  @media screen and (min-width: 2000px) {
    padding: 4rem 4rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
    padding: 4rem 2rem;
    height: auto;
    min-height: 100vh;
  }

  @media screen and (max-width: 400px) {
    padding: 4rem 1rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 2rem;
    justify-content: flex-start;
    margin-top: 15rem;
  }
  @media screen and (max-width: 450px) {
    margin-top: 15rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp {
    padding: 1rem 2rem;
    background: transparent;
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: none;
    border: none;
    align-items: center;
    justify-content: flex-start;
    margin-left: 20px; // Align with other elements
  }

  .role-section-container {
    align-self: flex-start; // Left aligned
  }

  .home-btn {
    align-self: flex-start; // Left aligned
  }

  .social-section {
    align-self: flex-start; // Left aligned
  }

  @media screen and (max-width: 1200px) {
    align-items: flex-start;
    justify-content: flex-start;

    .badge-cmp {
      margin-left: 20px;
    }

    .role-section-container {
      align-self: flex-start;
    }

    .home-btn {
      align-self: flex-start;
    }

    .social-section {
      align-self: flex-start;
    }
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  margin-left: 5rem;
  gap: 4rem;

  div {
    border-radius: 50%;
    background: var(--white-color);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;

    img {
      width: 60%;
      height: 60%;
    }

    &:hover {
      transform: scale(1.05);
      box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    }
  }

  // circles with different height & width
  div:nth-child(1) {
    width: 110px;
    height: 110px;
  }

  div:nth-child(2) {
    width: 160px;
    height: 160px;
  }

  div:nth-child(3) {
    width: 85px;
    height: 85px;
  }

  @media screen and (min-width: 2000px) {
    margin-left: 4rem;
    gap: 1.5rem;

    div:nth-child(2) {
      width: 320px;
      height: 320px;
    }

    div:nth-child(3) {
      width: 150px;
      height: 150px;
    }

    div:nth-child(1) {
      width: 180px;
      height: 180px;
    }
  }

  @media screen and (max-width: 1200px) {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 0;
    margin-top: 3rem;
    gap: 1.2rem;
    padding: 0 1rem;

    div:nth-child(1) {
      width: 90px;
      height: 90px;
    }

    div:nth-child(2) {
      width: 130px;
      height: 130px;
    }

    div:nth-child(3) {
      width: 75px;
      height: 75px;
    }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-left: 4rem;
  img {
    width: 160%;
    max-width: 750px;
    object-fit: contain;
    z-index: 1;

    @media screen and (min-width: 2000px) {
      max-width: 900px;
    }

    @media screen and (max-width: 1200px) {
      width: 140%;
      max-width: 520px;
      margin-left: -6rem;
      padding-left: 0;
    }

    @media screen and (max-width: 400px) {
      width: 130%;
      max-width: 380px;
    }
  }

  .overlay_circle {
    position: absolute;
    left: 55%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 0;
    width: 65%;
    height: 65%;
    max-width: 320px;
    max-height: 320px;

    @media screen and (min-width: 2000px) {
      max-width: 420px;
      max-height: 420px;
    }

    @media screen and (max-width: 1200px) {
      width: 60%;
      height: 60%;
      max-width: 260px;
      max-height: 260px;
    }

    @media screen and (max-width: 400px) {
      width: 65%;
      height: 65%;
      max-width: 220px;
      max-height: 220px;
    }
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

.p-text-hello {
  font-size: 15px;
}
