.app__about-container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  margin: 3rem auto;
}

.about-box {
  background: var(--primary-color); // Blueish background
  border: 2px solid var(--lightGray-color);
  border-radius: 20px;
  padding: 3rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;

  &:hover {
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15);
    transform: translateY(-5px);
  }

  @media screen and (min-width: 2000px) {
    padding: 4rem;
    border-radius: 25px;
  }

  @media screen and (max-width: 768px) {
    padding: 2rem;
    border-radius: 15px;
  }

  @media screen and (max-width: 450px) {
    padding: 1.5rem;
  }
}

.about-header {
  text-align: center;
  margin-bottom: 2.5rem;

  .about-title {
    font-size: 2.5rem;
    font-weight: 800;
    color: var(--black-color);
    margin-bottom: 1rem;

    @media screen and (min-width: 2000px) {
      font-size: 4rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 2rem;
    }
  }

  .title-underline {
    width: 80px;
    height: 4px;
    background: var(--secondary-color);
    margin: 0 auto;
    border-radius: 2px;

    @media screen and (min-width: 2000px) {
      width: 120px;
      height: 6px;
    }
  }
}

.about-content {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;

  @media screen and (min-width: 2000px) {
    gap: 3.5rem;
  }
}

.about-intro {
  .about-text {
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--gray-color);
    margin-bottom: 1.5rem;
    text-align: left;

    &:last-child {
      margin-bottom: 0;
    }

    .highlight {
      color: var(--secondary-color);
      font-weight: 600;
    }

    @media screen and (min-width: 2000px) {
      font-size: 1.5rem;
      line-height: 2;
    }

    @media screen and (max-width: 450px) {
      font-size: 1rem;
      line-height: 1.7;
    }
  }
}

.about-skills {
  .skills-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--black-color);
    margin-bottom: 1.5rem;
    text-align: center;

    @media screen and (min-width: 2000px) {
      font-size: 2rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 1.3rem;
    }
  }

  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;

    @media screen and (max-width: 600px) {
      grid-template-columns: 1fr;
      gap: 1rem;
    }
  }

  .skill-item {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.5rem;
    background: var(--white-color); // White background
    border-radius: 15px;
    border: 1px solid var(--lightGray-color);
    transition: all 0.3s ease-in-out;

    &:hover {
      transform: translateY(-3px);
      box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
      border-color: var(--secondary-color);
    }

    .skill-icon {
      font-size: 2rem;
      min-width: 50px;
      text-align: center;

      @media screen and (min-width: 2000px) {
        font-size: 2.5rem;
        min-width: 60px;
      }
    }

    .skill-content {
      h4 {
        font-size: 1.1rem;
        font-weight: 600;
        color: var(--black-color);
        margin-bottom: 0.3rem;

        @media screen and (min-width: 2000px) {
          font-size: 1.4rem;
        }

        @media screen and (max-width: 450px) {
          font-size: 1rem;
        }
      }

      p {
        font-size: 0.9rem;
        color: var(--gray-color);
        margin: 0;

        @media screen and (min-width: 2000px) {
          font-size: 1.2rem;
        }

        @media screen and (max-width: 450px) {
          font-size: 0.85rem;
        }
      }
    }

    @media screen and (max-width: 450px) {
      padding: 1rem;
      gap: 0.8rem;
    }
  }
}

.about-stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 2rem;
  background: var(--white-color); // White background
  border-radius: 15px;
  border: 1px solid var(--lightGray-color);

  .stat-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    .stat-number {
      font-size: 2.5rem;
      font-weight: 800;
      color: var(--secondary-color);
      margin-bottom: 0.5rem;

      @media screen and (min-width: 2000px) {
        font-size: 3.5rem;
      }

      @media screen and (max-width: 450px) {
        font-size: 2rem;
      }
    }

    .stat-label {
      font-size: 0.9rem;
      color: var(--gray-color);
      font-weight: 500;

      @media screen and (min-width: 2000px) {
        font-size: 1.2rem;
      }

      @media screen and (max-width: 450px) {
        font-size: 0.8rem;
      }
    }
  }

  @media screen and (max-width: 600px) {
    flex-direction: column;
    gap: 1.5rem;
    padding: 1.5rem;
  }
}

.about-journey {
  .journey-title {
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--black-color);
    margin-bottom: 1rem;
    text-align: center;

    @media screen and (min-width: 2000px) {
      font-size: 2rem;
    }

    @media screen and (max-width: 450px) {
      font-size: 1.3rem;
    }
  }

  .journey-text {
    font-size: 1.1rem;
    line-height: 1.8;
    color: var(--gray-color);
    text-align: center;
    max-width: 800px;
    margin: 0 auto;

    @media screen and (min-width: 2000px) {
      font-size: 1.5rem;
      line-height: 2;
    }

    @media screen and (max-width: 450px) {
      font-size: 1rem;
      line-height: 1.7;
    }
  }
}
