.wave {
  animation-name: wave-animation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
  font-size: 2.5rem;

  @media screen and (min-width: 2000px) {
    font-size: 5rem;
  }
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

#home {
  position: relative;
  background: url("../../assets/bgIMG.png");
  background-size: cover;
  background-repeat: repeat;
  background-position: center;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;
  padding: 6rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 400px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.35;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
    margin-bottom: 2rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: right;
    }

    @media screen and (max-width: 1200px) {
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
}

.app__header-summary {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  padding-left: 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    padding-left: 0;
    align-items: center;
  }

  @media screen and (max-width: 400px) {
    padding-left: 0;
  }
}

.summary-container {
  margin: 2rem 0;
  background: var(--white-color);
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  max-width: 750px;
  width: 100%;

  @media screen and (min-width: 2000px) {
    padding: 4rem;
    border-radius: 30px;
    max-width: 1000px;
  }

  @media screen and (max-width: 400px) {
    padding: 1.5rem;
    border-radius: 15px;
  }
}

.summary-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--black-color);
  margin-bottom: 1rem;
  text-align: left;

  @media screen and (min-width: 2000px) {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
  }

  @media screen and (max-width: 400px) {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
  }
}

.summary-text {
  font-size: 0.9rem;
  line-height: 1.6;
  color: var(--gray-color);
  margin-bottom: 1.25rem;
  text-align: left;

  @media screen and (min-width: 2000px) {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }

  @media screen and (max-width: 400px) {
    font-size: 0.8rem;
    margin-bottom: 1rem;
  }
}

.summary-skills {
  margin-bottom: 2rem;

  @media screen and (min-width: 2000px) {
    margin-bottom: 2.5rem;
  }

  @media screen and (max-width: 400px) {
    margin-bottom: 1.5rem;
  }
}

.skills-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--black-color);
  margin-bottom: 1rem;
  text-align: left;

  @media screen and (min-width: 2000px) {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }

  @media screen and (max-width: 400px) {
    font-size: 1rem;
    margin-bottom: 0.75rem;
  }
}

.skills-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;

  @media screen and (min-width: 600px) {
    grid-template-columns: 1fr 1fr;
  }

  @media screen and (min-width: 2000px) {
    gap: 1.5rem;
  }

  @media screen and (max-width: 400px) {
    gap: 0.75rem;
  }
}

.skill-category {
  background: rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  padding: 1rem;
  border-left: 3px solid var(--secondary-color);

  @media screen and (min-width: 2000px) {
    padding: 1.5rem;
    border-radius: 16px;
    border-left-width: 5px;
  }

  @media screen and (max-width: 400px) {
    padding: 0.75rem;
    border-radius: 8px;
  }

  h4 {
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--black-color);
    margin-bottom: 0.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 1.4rem;
      margin-bottom: 0.75rem;
    }

    @media screen and (max-width: 400px) {
      font-size: 0.8rem;
      margin-bottom: 0.25rem;
    }
  }

  p {
    font-size: 0.75rem;
    color: var(--gray-color);
    line-height: 1.4;
    margin: 0;

    @media screen and (min-width: 2000px) {
      font-size: 1.2rem;
    }

    @media screen and (max-width: 400px) {
      font-size: 0.7rem;
    }
  }
}

.summary-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;

  @media screen and (max-width: 500px) {
    flex-direction: column;
    gap: 0.75rem;
  }
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex: 1;
  padding: 0.5rem;

  @media screen and (max-width: 500px) {
    width: 100%;
    padding: 0.25rem;
  }
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 800;
  color: var(--secondary-color);
  margin-bottom: 0.25rem;

  @media screen and (min-width: 2000px) {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
  }

  @media screen and (max-width: 400px) {
    font-size: 1.25rem;
    margin-bottom: 0.125rem;
  }
}

.stat-label {
  font-size: 0.7rem;
  color: var(--gray-color);
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.5px;

  @media screen and (min-width: 2000px) {
    font-size: 1.2rem;
  }

  @media screen and (max-width: 400px) {
    font-size: 0.6rem;
  }
}

.p-text-hello {
  font-size: 15px;
}
